import React,{useRef} from 'react'
import { collection} from "firebase/firestore"
import { query, where,updateDoc,doc } from "firebase/firestore";  
import {useCollectionData} from "react-firebase-hooks/firestore"
import Logo from "../imges/A N I M O.png"
import { signOut } from "firebase/auth"
import {auth , db} from "../firebase-config"
import "./header.css"
import { Link } from "react-router-dom";
import Swal from "sweetalert2";  

export default function Header() {


    const userCollectionRef = collection(db,"users");
    const queryuser = query(userCollectionRef,where("uid","==",auth.currentUser.uid));
    
    const [userData] = useCollectionData(queryuser,{idField:'id'});
    if(userData)
    {
        localStorage.setItem("codetime",userData[0].codetime);
    }

    const line1 = useRef(null);
    const line2 = useRef(null);
    const line3 = useRef(null);
    const uln = useRef(null);

    
    const burgerclickhandle = ()=>{
        line1.current.classList.toggle("activeline1")
        line2.current.classList.toggle("activeline2")
        line3.current.classList.toggle("activeline3")
        uln.current.classList.toggle("active")
    }

    const logoutdo=async()=>{
        Swal.fire({
            title: '   ',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: ' ',
            cancelButtonText:""
        }).then((result) => {
            if (result.isConfirmed) {
                localStorage.setItem("codetime",0);
                signOut(auth);
            }
        })
    }

    const notipanel = useRef(null);
    const notiback = useRef(null);
    const opennoti=()=>{
        notipanel.current.style.display="block";
        notiback.current.style.display="block";
    }

    const closenoti=async()=>{
        notipanel.current.style.display="none";
        notiback.current.style.display="none";
        let data =userData&&userData[0];
        data.notifications=[];
        const upddoc = doc(db,"users",userData&&userData[0].id);
        await updateDoc(upddoc,data);
    }
    return (
        <div className="headerf">
            <div className="container">
                <header>
                    <Link to={process.env.PUBLIC_URL +"/"}>
                    <div className="logoside">
                        <img src={Logo} alt="Logo"/>
                        <p>    </p>
                    </div>
                    </Link>
                    <div className="profile">
                        <i className="fas fa-sign-out-alt" onClick={logoutdo}></i>
                        <div className="status">
                            <div className="level">
                            <span>Lv</span> {userData && userData[0].level}
                            </div>
                            <div className="exp">
                            <span>EXP</span>
                            <div className="bar">
                                <div className="progress" style={{width:`${(userData&&userData[0].exp/((userData&&userData[0].level+1)*1000))*100}%`}}></div>
                                <div className="progressdetails">{userData&&userData[0].exp + "/" + (userData&&userData[0].level+1)*1000 }</div>
                            </div>
                            </div>
                        </div>
                        {userData&&userData[0].notifications.length>0?(<span className="noti" onClick={opennoti}>{userData&&userData[0].notifications.length}</span>):""}
                        {userData&&userData[0].notifications.length>0?<img className="pointer" src={auth.currentUser.photoURL} onClick={opennoti} alt="userphoto"/>:<img src={auth.currentUser.photoURL}   alt="userphoto"/>}
                    </div>
                            
                    <div className="barburger" onClick={burgerclickhandle}>
                        <div className="slice" id="line1" ref={line1}></div>
                        <div className="slice" id="line2" ref={line2}></div>
                        <div className="slice" id="line3" ref={line3}></div>
                    </div>
                </header>
                <nav>
                    <ul ref={uln}>
                    <div className="profile">
                    <i className="fas fa-sign-out-alt" onClick={async()=> await signOut(auth)}></i>
                        <div className="status">
                            <div className="level">
                            <span>Lv</span> {userData && userData[0].level}
                            </div>
                            <div className="exp">
                            <span>EXP</span>
                            <div className="bar">
                                <div className="progress" style={{width:`${(userData&&userData[0].exp/((userData&&userData[0].level+1)*1000))*100}%`}}></div>
                                <div className="progressdetails">{userData&&userData[0].exp + "/" + (userData&&userData[0].level+1)*1000 }</div>
                            </div>
                            </div>
                        </div>
                        {userData&&userData[0].notifications.length>0?(<span className="noti" onClick={opennoti}>{userData&&userData[0].notifications.length}</span>):""}
                        {userData&&userData[0].notifications.length>0?<img className="pointer" src={auth.currentUser.photoURL} onClick={opennoti} alt="userphoto"/>:<img src={auth.currentUser.photoURL}   alt="userphoto"/>}
                    </div>
                        <Link to={process.env.PUBLIC_URL +"/"}><li></li></Link>
                        <a href="https://www.youtube.com/c/AnimoGameDev" target="_blank" rel="noreferrer"><li></li></a>
                        <a href="https://discord.gg/N6DaYAY"  target="_blank" rel="noreferrer"><li></li></a>
                        <Link to={process.env.PUBLIC_URL +"/addcode"}><li className="fakea1"></li></Link>
                        <a href="https://youtu.be/dAzsvDSB2Bs" target="_blank"  rel="noreferrer"><li className="fakea2">  </li></a>
                    </ul>
                </nav>
            </div>
            <div className="back" ref={notiback}></div>
            <div className="notipanel" ref={notipanel}>
                <i className="fas fa-times" onClick={closenoti}></i>
                <h2></h2>
                {userData&&userData[0].notifications.map((p,i)=> <p key={i} className="singlenoti">{p.text}</p> )}
            </div>
        </div>
    )
}
